

*{
  font-family: 'Inter';
  box-sizing: border-box;
  
}

.navbar-container{
  position: fixed;
  
  background-color: #143CA3;
  height: 9vh;
  top: 0;
  width: 100vw;
  padding: 0px;
  opacity: 0.98;
  backdrop-filter: blur(10px);
  z-index: 100;
  
}
.navbar-text{
  position: relative;
  text-align: center;
  color: #ffffff;
  bottom: 2rem;
  font-size: 3em;
  font-weight: bold;
}
.jumbo-text-container{
  padding-left: 1rem;
  padding-top: 4.5rem;
  position: relative;
}
.jumbo-text{
  font-size: 4rem;
  padding: 0rem;
  font-weight: bold;
  line-height: 20px;
}
.button-wrapper{
  height: 100%;
  width: 100%;
  background: #104DEA;
  filter: blur(7px);
  border-radius: 7px;
  position: absolute;

}
.button{
  background: #0C44D4;
  border-radius: 7px;
  z-index: 1;
  height: 3rem;
  width: 15rem;
  position: relative;
  margin-left: 1rem;
  cursor: pointer;
  text-align: center;
}
#button-text{
  z-index: 10;
  color: #ffffff;
  position: absolute;
  display: flex;
  align-items: center;
  text-align: center;
  padding-left: 4rem;
  font-weight: 600;
  bottom: -1px;
}
.info{
  margin-left: 1rem;
  margin-right: 2rem;
  padding-bottom: 1.2rem;
}
.info-text{
  font-weight: bold;
  font-size: 2.5rem;
  margin-bottom: 1rem;
}
.info-des{
  font-size: 1.2em;

}
#button-text-d{
  z-index: 10;
  color: #ffffff;
  position: absolute;
  display: flex;
  align-items: center;
  text-align: center;
  padding-left: 2.6rem;
  font-weight: 600;
  bottom: -1px;
  
}

.blu-area{
  background-color: #143CA3;
  color: #ffffff;
  margin-top: 3rem;
  padding-bottom: 2rem;;
}
.blu-tit{
  font-weight: bold;
  font-size: 2.5rem;
  margin-bottom: 1rem;
  margin-left: 1rem;
  padding-top: 2rem;
}
.blu-text{
  font-size: 1.2em;
  margin-bottom: 1rem;
  margin-left: 1rem;
  padding-top: 0.5rem;
}

.blu-button{
  background: #ffffff;
  border-radius: 7px;
  z-index: 1;
  height: 3rem;
  width: 22rem;
  margin-top: 2rem;
  position: relative;
  margin-left: 3rem;
  cursor: pointer;
  text-align: center;
}
.blu-button-wrapper{
  height: 100%;
  width: 100%;
  background: #CBD6F4;
  filter: blur(7px);
  border-radius: 7px;
  position: absolute;
}
#button-text-blu{
  z-index: 10;
  color: black;
  position: absolute;
  display: flex;
  align-items: center;
  text-align: center;
  padding-left: 6rem;
  font-size: 1.2rem;
  
  font-weight: 600;
  bottom: -8px;
}
.auth-blue{
  position: absolute;
  top: 5rem;
  width: 100vw;
  height: 100vh;
  background-color: #2B55C1;
}
.auth-title{
  margin-left: 1rem;
  font-size: 1.5em;
  margin-bottom: 1rem;
  margin-left: 1rem;
  padding-top: 0.5rem;
  font-weight: 600;
  color: #ffffff;
}
.auth-input-wrapper{
  display: flex;
  flex-direction: column;
}
.auth-input{
  margin-left: 1rem;
  margin-top: 1rem;
  height: 4rem;
  border-radius: 7px;
  border: none;
  padding-left:2rem ;
  font-size: 1.2em;
  width: 80vw;
}

svg{
  text-align: center;
  height: 20rem;
  margin-top: 5rem;
  margin-left: -05rem;
}
.add-wrapper{
  position: absolute;
  top: 5rem;
  width: 100vw;
  height: 100vh;
  
}
.add-title{
  margin-left: 1rem;
  font-size: 1.5em;
  margin-bottom: 1rem;
  margin-left: 1rem;
  padding-top: 0.5rem;
  font-weight: 600;
  
}
.editor{
  margin-left: 1rem;
  width: 90vw;
  /* height: 50v; */
}
.add-button-wrapper{
  margin-top: 2rem;
}